import React from "react";
import thunder from '../fetcher'
import useSWR from 'swr'
import { Button, IconLockClosed, Stack } from 'degen'





const fetcher = (address: string) => {
    return thunder('query')({
        crowdfundAtAddress: [{
            address: address
        }, {
            name: true,
            symbol: true,
            fundingRecipient: true,
            goal: true,
            publishStatus: true,
            createdAt: true,
        },
        ],
    }
    )
}


const Crowdfund = ({ address }: { address: string }): JSX.Element => {
    const { data, error, isValidating } = useSWR(address, fetcher);
    if (isValidating) {
        return <div>Loading...</div>
    }
    if (error) {
        return <div>Error! {error.message}</div>
    }
    return (
        <Stack align="center">
            <Button prefix={<IconLockClosed />}
                variant="primary"
                width={{ xs: 'full', md: 'max' }}>Fetch</Button>
            {JSON.stringify(data)}
        </Stack>
    )
};

export default Crowdfund;